import React from 'react';
import PropTypes from 'prop-types';
import {UNITS_TIME, UNIT_SECOND} from './../../constants';
import convert from 'convert-units';
import { FormLabel, FormNumberInput, FormSelect } from '../../components/style/export';
import {Map} from 'immutable';
import {toFixedFloat} from '../../utils/math';
import PropertyStyle from './shared-property-style';

const internalTableStyle = {borderCollapse: 'collapse'};
const secondTdStyle = {padding: 0};
const unitContainerStyle = {width: '5em'};

export default function PropertyTimeMeasure({value, onUpdate, onValid, configs, sourceElement, internalState, state}, {catalog}) {

  let time = value.get('time') || 0;
  let _time = value.get('_time') || time;
  let _unit = value.get('_unit') || UNIT_SECOND;
  let { hook, label, ...configRest} = configs;

  let update = (timeInput, unitInput) => {

    let newTime = toFixedFloat(timeInput);
    let merged = value.merge({
      time: unitInput !== UNIT_SECOND ? convert(newTime).from(unitInput).to(UNIT_SECOND) : newTime,
      _time: timeInput,
      _unit: unitInput
    });

    if (hook) {
      return hook(merged, sourceElement, internalState, state).then(val => {
        return onUpdate(val);
      });
    }

    return onUpdate(merged);
  };

  return (
    <table className="PropertyTimeMeasure" style={PropertyStyle.tableStyle}>
      <tbody>
      <tr>
        <td style={PropertyStyle.firstTdStyle}><FormLabel>{label}</FormLabel></td>
        <td style={secondTdStyle}>
          <table style={internalTableStyle}>
            <tbody>
            <tr>
              <td>
                <FormNumberInput
                  value={_time}
                  onChange={event => update(event.target.value, _unit)}
                  onValid={onValid}
                  {...configRest}
                />
              </td>
              <td style={unitContainerStyle}>
                <FormSelect value={_unit} onChange={event => update(_time, event.target.value) }>
                  {
                    UNITS_TIME.map(el => <option key={el} value={el}>{el}</option>)
                  }
                </FormSelect>
              </td>
            </tr>
            </tbody>
          </table>
        </td>
      </tr>
      </tbody>
    </table>
  );

}

PropertyTimeMeasure.propTypes = {
  value: PropTypes.instanceOf(Map).isRequired,
  onUpdate: PropTypes.func.isRequired,
  onValid: PropTypes.func,
  configs: PropTypes.object.isRequired,
  sourceElement: PropTypes.object,
  internalState: PropTypes.object,
  state: PropTypes.object.isRequired
};

PropertyTimeMeasure.contextTypes = {
  catalog: PropTypes.object.isRequired
};
