import React from 'react';
import { BoxGeometry, MeshBasicMaterial, Mesh, BoxHelper } from 'three';
import { ReactPlannerSharedStyle } from 'react-planner';

export default {
  name: 'Movement',
  prototype: 'items',

  info: {
    title: 'New Movement',
    tag: ['movements'],
    description: 'Movement',
    image: require('./cube.png')
  },

  properties: {
    capacity: {
      label: 'Capacity',
      type: 'string',
      defaultValue: ''
    },
    stagenumber:{
      label: 'Stage Number',
      type: 'number',
      defaultValue: ''
    },
    itemcategory: {
      label: 'Item Category',
      type: 'string',
      defaultValue: ''
    },
    phaseparallelity: {
      label: 'Phase parallelity',
      type: 'string',
      defaultValue: ''
    },
    color: {
      label: 'Color',
      type: 'color',
      defaultValue: ReactPlannerSharedStyle.AREA_MESH_COLOR.unselected
    },
    distance: {
      label: 'Distance',
      type: 'length-measure',
      defaultValue: {
        value:'',
        unit:'m'
      }
    },
    
    loadtime: {
      label: 'Load Time',
      type: 'time',
      defaultValue: {
        value:'',
        unit:'s'
      }
    },
    unloadtime: {
      label: 'Unload Time',
      type: 'time',
      defaultValue: {
        value:'',
        unit:'s'
      }
    },
   
    width: {
      label: 'Width',
      type: 'length-measure',
      defaultValue: {
        length: 100,
        unit: 'cm'
      }
    },
    travelbackdistance: {
      label: 'Travel Back Distance',
      type: 'length-measure',
      defaultValue: {
        length: 0,
        unit: 'cm'
      }
    },
    depth: {
      label: 'Depth',
      type: 'length-measure',
      defaultValue: {
        length: 100,
        unit: 'cm'
      }
    },
    movementtype: {
      label: 'Movement Type',
      type: 'string',
      defaultValue: ''
    },
    
  },

  render2D: (element, layer, scene) => {
    let style = {
      stroke: !element.selected ? ReactPlannerSharedStyle.LINE_MESH_COLOR.unselected : ReactPlannerSharedStyle.MESH_SELECTED,
      strokeWidth: 2,
      fill: element.properties.get('color')
    };

    let w = element.properties.getIn(['width', 'length']);
    let d = element.properties.getIn(['depth', 'length']);
   
    let w2 = w / 2;
    let d2 = d / 2;

    return (
      <g transform={`translate(-${w2}, -${d2})`}>
        <rect x="0" y="0" width={w} height={d} style={style} />
      </g>
    );
  },

  render3D: (element, layer, scene) => {
    let w = element.properties.getIn(['width', 'length']);
    //let h = element.properties.getIn(['height', 'length']);
    let h=100;
    let d = element.properties.getIn(['depth', 'length']);
    
    let geometry = new BoxGeometry(w, h, d);
    let material = new MeshBasicMaterial({
      color: element.properties.get('color')
    });

    let mesh = new Mesh(geometry, material);

    let box = new BoxHelper(mesh, !element.selected ? ReactPlannerSharedStyle.LINE_MESH_COLOR.unselected : ReactPlannerSharedStyle.MESH_SELECTED );
    box.material.linewidth = 2;
    box.renderOrder = 1000;
    mesh.add(box);

    mesh.position.y = (h / 2);

    return Promise.resolve(mesh);
  }
};
