import React, { useState } from 'react';
import Panel from './panel';
import axios from 'axios';



const SavedLayouts = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [companyName, setCompanyName] = useState('17722518287036'); // Default company name
  const [posts, setPosts] = useState([]);
  const [isLoggedIn, setLoggedIn] = useState(false);

  const [tableData, setTableData] = useState([]);

  const [layoutCount, setLayoutCount] = useState(0);

  const apiURL = 'https://ainakcloud.herokuapp.com';

  
  let convertedData;

  const fetchJsonDataa = (url) => {
    // Your existing fetchJsonData function code here
    // ...
    let startIndex = url.lastIndexOf("=");
    if (startIndex != -1)
      startIndex = url.slice(startIndex + 1);
    const id = url.slice(url.lastIndexOf("=") + 1);
    if (!id) {
      alert("Invalid link format. Please provide a valid link with an ID.");
      return;
    }
    const apiUrl = 'https://jsonmanager.herokuapp.com/api/' + id;
    // Rest of the code inside fetchJsonData function remains the same
    // ...
    fetch(apiUrl)
      .then(response => {
        if (!response.ok) {
          throw new Error("Network response was not ok.");
        }
        return response.json();
      })
      .then(data => {
        // Handle the fetched JSON data
        // You can process the data or assign it to the convertedData variable as needed.
        convertedData = data;

        // Now, create a new FileReader and store the JSON data in it.
        const reader = new FileReader();
        reader.addEventListener('load', function (event) {
          // The JSON data will be stored in reader.result
          const jsonData = event.target.result;
          const dataObject = JSON.parse(jsonData);
          const calibrationPoints = dataObject.calibrationJson;
          const verticesData = calibrationPoints.map((point, index) => {
            const newVertexId = "CalibVertex_" + index; // Generate the new vertex id

            const vertexData = {
              id: newVertexId,
              type: "",
              prototype: "vertices",
              name: "Vertex",
              misc: {},
              selected: false,
              properties: {},
              visible: true,
              x: (point.position.x * 100) - 100, // Assign the x value
              y: (point.position.y * 100) - 100, // Assign the y value
              // z: point.position.z * 100, // If you want to include z value, uncomment this line
              lines: [],
              areas: []
            };


            return vertexData;
          });
          const wallData = {
            id: "wallId",
            type: "wall",
            prototype: "lines",
            name: "Wall",
            misc: {
              "_unitLength": "cm"
            },
            selected: false,
            properties: {
              height: {
                length: 300
              },
              thickness: {
                length: 20
              },
              textureA: "bricks",
              textureB: "bricks"
            },
            visible: true,
            vertices: [
              "13xEkXDRRr", // Example vertex ID (replace with your actual vertex ID)
              "vertexId" // Example vertex ID (replace with your actual vertex ID)
            ],
            holes: []
          };




          // Create the desired format object with calibration point data in vertices
          convertedData = {
            unit: "cm",
            layers: {
              "layer-1": {
                id: "layer-1",
                altitude: 0,
                order: 0,
                opacity: 1,
                name: "default",
                visible: true,
                vertices: {}, // Change to an empty object
                lines: {},
                holes: {},
                areas: {},
                items: {},
                selected: {
                  vertices: [],
                  lines: [],
                  holes: [],
                  areas: [],
                  items: [],
                },
              },
            },
            grids: {
              h1: {
                id: "h1",
                type: "horizontal-streak",
                properties: {
                  step: 20,
                  colors: ["#808080", "#ddd", "#ddd", "#ddd", "#ddd"],
                },
              },
              v1: {
                id: "v1",
                type: "vertical-streak",
                properties: {
                  step: 20,
                  colors: ["#808080", "#ddd", "#ddd", "#ddd", "#ddd"],
                },
              },
            },
            selectedLayer: "layer-1",
            groups: {},
            width: 3000,
            height: 2000,
            meta: {},
            guides: {
              horizontal: {},
              vertical: {},
              circular: {},
            },
          };


          // Find the minimum x and y values among all the vertices
          let minX = Infinity;
          let minY = Infinity;

          for (const vertex of verticesData) {
            if (vertex.x < minX) {
              minX = vertex.x;
            }
            if (vertex.y < minY) {
              minY = vertex.y;
            }
          }

          // Calculate the adjustment values
          const xAdjustment = (minX) - 100;
          const yAdjustment = (minY) - 100;


          let x1 = 0;
          let x2 = 0;
          let y1 = 0;
          let y2 = 0;
          let j = 0;
          // Update the x and y coordinates of all the vertices with the adjustment values
          for (const vertex of verticesData) {
            // vertex.x += xAdjustment + 100;
            // vertex.y += yAdjustment + 100;
            vertex.x = (vertex.x - xAdjustment)
            vertex.y = (vertex.y - yAdjustment)
            if (j == 0) {
              x1 = vertex.x;
              y1 = vertex.y;
            }
            if (j == 1) {
              x2 = vertex.x;
              y2 = vertex.y;
            }
            j++;
          }


          // Find the maximum x and y values among all the vertices after adjusting them
          let maxX = -Infinity;
          let maxY = -Infinity;

          for (const vertex of verticesData) {
            if (vertex.x > maxX) {
              maxX = vertex.x;
            }
            if (vertex.y > maxY) {
              maxY = vertex.y;
            }
          }
          // Calculate the adjusted width and height
          const adjustedWidth = maxX //+ 100;
          const adjustedHeight = maxY //+ 100;

          // Update the width and height properties of the convertedData object
          convertedData.width = adjustedWidth + 100;
          convertedData.height = adjustedHeight + 100;

          convertedData.layers["layer-1"].vertices = Object.assign({}, ...verticesData.map(vertex => ({ [vertex.id]: vertex })));

          function createWallBetweenVertices(vertexId1, vertexId2) {
            const newWallId = "AinakARWall_" + Math.random().toString(36).substr(2, 9); // Generate a unique wall ID

            const newWall = JSON.parse(JSON.stringify(wallData)); // Deep copy the wallData template
            newWall.id = newWallId;
            newWall.vertices = [vertexId1, vertexId2]; // Set the vertices property with the provided vertex IDs

            // Add the new wall to the convertedData object under the 'lines' section
            convertedData.layers["layer-1"].lines[newWallId] = newWall;
          }

          const vertexIds = verticesData.map(vertex => vertex.id);

          // Create walls between consecutive vertices using a for loop
          for (let i = 0; i < vertexIds.length - 1; i++) {
            const vertexId1 = vertexIds[i];
            const vertexId2 = vertexIds[i + 1];
            createWallBetweenVertices(vertexId1, vertexId2);
          }

          // Connect the last vertex with the first vertex to complete the polygon
          const firstVertexId = vertexIds[0];
          const lastVertexId = vertexIds[vertexIds.length - 1];
          createWallBetweenVertices(lastVertexId, firstVertexId);


          //AREA
          function createAreaWithVertices(vertexIds) {
            const newAreaId = "Area_" + Math.random().toString(36).substr(2, 9); // Generate a unique area ID

            const newArea = {
              id: newAreaId,
              type: "area",
              prototype: "areas",
              name: "Area",
              misc: {},
              selected: false,
              properties: {
                patternColor: "#F5F4F4",
                thickness: {
                  length: 0
                },
                texture: "none"
              },
              visible: true,
              vertices: vertexIds,
              holes: []
            };

            // Add the new area to the convertedData object under the 'areas' section
            convertedData.layers["layer-1"].areas[newAreaId] = newArea;
          }

          // Call the function with the vertex IDs
          //const vertexIds = verticesData.map(vertex => vertex.id);
          createAreaWithVertices(vertexIds);


          //ITEMS
          const modelJsonArray = dataObject.modelJson;

          // Step 2: Create a dictionary to store model data
          const modelData = {};

          // Step 3: Iterate through "modelJson" array and associate with "items"
          let modelIndex = 0;


          // Extract the x and y values for newVertex_1 and newVertex_2

          const diffX = x1 - x2;
          const diffY = y1 - y2;
          const angle = (Math.atan2(diffY, diffX) * 180) / Math.PI;

          let theta1;
          if (x1 - x2 > 0 && y1 - y2 > 0) {
            theta1 = angle; // Quadrant I
          } else if (x1 - x2 < 0 && y1 - y2 > 0) {
            theta1 = 180 - angle; // Quadrant II
          } else if (x1 - x2 < 0 && y1 - y2 < 0) {
            theta1 = 180 + angle; // Quadrant III
          } else if (x1 - x2 > 0 && y1 - y2 < 0) {
            theta1 = 360 - angle; // Quadrant IV
          } else {
            theta1 = angle; // On one of the axes
          }

          let correctedTheta;
          if (x1 - x2 > 0 && y1 - y2 > 0) {
            correctedTheta = theta1; // Quadrant I
          } else if (x1 - x2 < 0 && y1 - y2 > 0) {
            correctedTheta = 180 - theta1; // Quadrant II
          } else if (x1 - x2 < 0 && y1 - y2 < 0) {
            correctedTheta = 180 + theta1; // Quadrant III
          } else if (x1 - x2 > 0 && y1 - y2 < 0) {
            correctedTheta = 360 - theta1; // Quadrant IV
          } else {
            correctedTheta = theta1; // On one of the axes
          }

          let angle3;
          let rot;
          let angle2;
          let modelrotation;
          for (const model of modelJsonArray) {

            const itemId = 'AinakModel_' + model.id + '_' + modelIndex;
            const itemType = model.fileName.slice(0, -4);
            const itemName = itemId + '_' + itemType;
            const itemX = (model.position.x * 100) - xAdjustment - 100;
            const itemY = (model.position.y * 100) - yAdjustment - 100;

            modelrotation = model.rotation.z;
            rot = 360 - modelrotation;
            angle2 = correctedTheta + rot;
            angle3 = angle2 - 180;

            // Create a new item object with the model data
            const itemData = {
              id: itemId,
              type: itemType,
              prototype: "items",
              name: itemName,
              misc: {},
              selected: false,
              properties: {
                details: "",
                depth: {
                  length: 100,
                  unit: "cm"
                },
                color: "#F5F4F4",
                width: {
                  length: 100,
                  unit: "cm"
                },
                height: {
                  length: 100,
                  unit: "cm"
                },
                parallelity: 1,
                number: "",
                time: 1,
                factor: 1,
                duration: 1
              },
              visible: true,
              x: itemX,
              y: itemY,
              rotation: angle3
            }
            modelIndex++;
            // Associate the itemData with the itemId in the 'items' object
            convertedData.layers["layer-1"].items[itemId] = itemData;
          }
          const convertedJSON = JSON.stringify(convertedData, null, 2);
          const filename = 'converted_data.json';
          const blob = new Blob([convertedJSON], { type: 'application/json' });

          if (window.navigator.msSaveOrOpenBlob) {
            // For IE and Edge browsers
            window.navigator.msSaveBlob(blob, filename);
          } else {
            // For other modern browsers
            const url = window.URL.createObjectURL(blob);
            const a = document.createElement('a');
            a.href = url;
            a.download = filename;
            document.body.appendChild(a);
            a.click();
            window.URL.revokeObjectURL(url);
            document.body.removeChild(a);
          }
        });
        reader.readAsText(new Blob([JSON.stringify(convertedData)], { type: 'application/json' }));
      })
      .catch(error => {
        console.error("Error fetching JSON data:", error);
      });
  };

  const login = () => {
    const credentials = {
      username: username,
      password: password,
    };

    axios
      .post(`${apiURL}/login/`, credentials)
      .then((response) => {
        setCompanyName(username);
        setLoggedIn(true);

        // Additional logic if needed after successful login

        // Call populateList to load data after login
        populateList();
      })
      .catch((error) => {
        console.log('Login Failed', error);

        // Handle login failure, you might want to show an error message to the user
      });
  };

  const refresh = () => {
    // Clear the existing content in the table
    document.getElementById('posts').innerHTML = "";

    // Populate the table with fresh data
    populateList();

  };

  const populateList = () => {
    let apiUrl;

    if (companyName === 'all') {
      apiUrl = 'https://jsonmanager.herokuapp.com/api/';
    } else {
      apiUrl = 'https://jsonmanager.herokuapp.com/api/';  // Modify this URL or add logic for specific company data
    }

    axios
      .get(apiUrl)
      .then((response) => {
        if (companyName === 'all') {
          populate(response.data.json);
        } else {
          populateCompany(response.data.json);
        }
      })
      .catch((error) => {
        console.error('Error fetching data', error);

        // Handle error, you might want to show an error message to the user
      });
  };
  const populate = (posts_array) => {
    let t = [];
    for (let i = posts_array.length - 1; i >= 0; i--) {
      t.push(traverse(posts_array, i, true));
    }
    setTableData(t);
    setLayoutCount(t.length); // Update the layout count here

  }

  const populateCompany = (posts_array) => {
    let t = [];
    let count = 0;
    for (let i = posts_array.length - 1; i >= 0; i--) {
      if (posts_array[i].seller === companyName) {
        t.push(traverse(posts_array, i, false));
        count++;
      }
    }
    setTableData(t);
    if (count === 0) {
      setUsername(`Number of Layouts: ${count}`);
    }
    setLayoutCount(t.length);

  }
  const traverse = (posts_array, i, showCompanyName) => {
    let newdatestring = convertUTCDateToLocalDate(new Date(posts_array[i].dt)).toLocaleString();
    let row = [];

    row.push(`https://ainak.io/layoutplanner/?j=${posts_array[i].id}`);
    row.push("");
    row.push(newdatestring.split(", "));
    row.push(posts_array[i].name);
    //row.push(posts_array[i].email);


    if (showCompanyName) {
      row.push(posts_array[i].seller);
    }

    return row;
  }

  const convertUTCDateToLocalDate = (date) => {
    var newDate = new Date(date.getTime() + date.getTimezoneOffset() * 60 * 1000);
    var offset = date.getTimezoneOffset() / 60;
    var hours = date.getHours();
    newDate.setHours(hours - offset);
    return newDate;
  }

  // const convertTo24h = (time_str) => {
  //   var time = time_str.match(/(\d+):(\d+):(\d+) (\w)/);
  //   var hours = Number(time[1]);
  //   var minutes = Number(time[2]);
  //   var seconds = Number(time[3]);
  //   var meridian = time[4].toLowerCase();

  //   if (meridian == 'p' && hours < 12) 
  //     hours += 12;    
  //   else if (meridian == 'a' && hours == 12) 
  //     hours -= 12;    

  //   return ddForTime(hours) + ":" + ddForTime(minutes) + ":" + ddForTime(seconds);
  // };

  const ddDate = (date) => {
    return dd(date.split("/")[1]) + "/" + dd(date.split("/")[0]) + "/" + dd(date.split("/")[2]);
  }

  const dd = (H) => {
    if (H.length == 1)
      return "0" + H;
    else
      return H;
  }
  // Use the fetch API for making HTTP requests



  // const ddForTime = (H) => {
  //   if(H<10)
  //       return "0"+H;
  //   else
  //       return H;
  // }

  // Other functions and JSX structure...

  return (

    <Panel name={('Layout List')}>
      <div>
        
    
        {isLoggedIn ? (
          // Render content after login
          <div>

            <button id="refresh" type="button" onClick={refresh} style={{ marginLeft: '10px', marginTop: '10px', padding: '10px 15px', background: '#007BFF', color: 'white', border: 'none', borderRadius: '4px', cursor: 'pointer' }}>
              Refresh
            </button>
            {layoutCount > 0 && <p>Number of Layouts: {layoutCount}</p>}
            <div style={{ overflow: 'auto', maxHeight: '300px', scrollbarWidth: 'thin', scrollbarColor: '#007BFF white' }}>
              <table id="postss" className="unstyledTablee" style={{ width: '100%', borderCollapse: 'collapse' }}>
                <thead>
                  <tr>
                    <th style={{ padding: '10px', borderBottom: '1px solid #ddd' }}>Link to Xperience</th>
                    <th style={{ padding: '10px', borderBottom: '1px solid #ddd' }}>Convert</th>
                    <th style={{ padding: '10px', borderBottom: '1px solid #ddd' }}>Date and Time</th>
                    <th style={{ padding: '10px', borderBottom: '1px solid #ddd' }}>Name</th>

                  </tr>
                </thead>
                <tbody>
                  {tableData.map((row, index) => (
                    <tr key={index}>
                      {/* Assuming each row is an array of table cells */}
                      {row.map((cell, i) =>
                        i === 0
                          ? <td key={i} style={{ padding: '5px', borderBottom: '1px solid #ddd' }}><a href={cell} target="_blank" rel="noopener noreferrer" style={{ color: 'white' }}>{cell}</a></td>
                          : i === 1
                            ? <td key={i} style={{ padding: '10px', borderBottom: '1px solid #ddd'}}>
                              <button type="button" onClick={() => fetchJsonDataa(row[0])} style={{ padding: '5px 10px', background: '#007BFF', color: 'white', border: 'none', borderRadius: '4px', cursor: 'pointer' }}>
                                Convert
                              </button>
                            </td>
                            : <td key={i} style={{ padding: '10px', borderBottom: '1px solid #ddd' }}>{cell}</td>
                      )}
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
            {/* Table and other components... */}
          </div>
        ) : (
          // Render login form if not logged in
          <div id="Login_ui" style={{ display: 'flex', height: '100vh' }}>
            <div className="container" style={{ width: '300px', padding: '20px' }}>
              <label htmlFor="username" style={{ marginBottom: '10px', display: 'block' }}>
                <b>Username</b>
              </label>
              <input
                type="text"
                id="username"
                placeholder="Enter Username"
                name="username"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                required
                style={{ marginBottom: '20px', padding: '10px', width: '100%' }}
              />

              <label htmlFor="password" style={{ marginBottom: '10px', display: 'block' }}>
                <b>Password</b>
              </label>
              <input
                type="password"
                id="password"
                placeholder="Enter Password"
                name="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
                style={{ marginBottom: '20px', padding: '10px', width: '100%' }}
              />

              <button type="button" onClick={login} style={{ padding: '10px 20px', background: '#007BFF', color: 'white', border: 'none', borderRadius: '4px', cursor: 'pointer' }}>
                Login
              </button>
            </div>
          </div>
        )}
      </div>
    </Panel>
  );
};
export default SavedLayouts;

//export default JsonConverter;

