import React, { useState } from 'react';
import 'regenerator-runtime/runtime';
import Panel from './panel';




function JSONUploader() {
  const [firstname, setFirstname] = useState('');
  //const [lastname, setLastname] = useState('');
  const [email, setEmail] = useState('');
  const [seller, setSeller] = useState('');
  const [message, setMessage] = useState('');

  const handleFormSubmit = async (event) => {
    event.preventDefault();

    const jsonFile = document.getElementById('jsonFile').files[0];

    if (jsonFile) {
      try {
        const reader = new FileReader();

        reader.onload = async (event) => {
          const jsonData = JSON.parse(event.target.result);
          // Rest of the JSON parsing logic...

          const desiredData = {
            annotationsJson: [],
            box: [],
            calibrationJson: [],
            imagesJson: [],
            measurementsJson: [],
            modelJson: [],
            planePosition: { x: 0, y: 0, z: 0 },
            qrJson: []
          };

          // Extract vertices from input JSON and add them to calibrationJson
          const vertices = jsonData.layers['layer-1'].vertices;
          var i = 1;
          let isFirstLoop = true; // Variable to track the first loop iteration
          //let firstCalibrationPoint; // Variable to store the calibration point from the first iteration

          let iterationCount = 0;
          let firstCalibrationPoint = null; // Initialize with null
          let k=0;

          let x1 = 0;
          let x2 = 0;
          let y1 = 0;
          let y2 = 0;
          for (const vertexId in vertices) {
           

            if (iterationCount === Object.keys(vertices).length) {
              break; // Exit the loop after one iteration less than the vertices
            }

            const vertex = vertices[vertexId];
            if (k == 0) {
              x1 = vertex.x;
              y1 = vertex.y;
            }
            if (k == 1) {
              x2 = vertex.x;
              y2 = vertex.y;
            }
            k++;
            const calibrationPoint = {
              customText: `CalibrationPoint_${iterationCount}`,
              height: 4,
              isLinkedToPrevious: iterationCount !== 0,
              position: { x: vertex.x / 100, y: vertex.y / 100, z: 0 },
            };
           

            if (iterationCount === 0) {
              firstCalibrationPoint = { ...calibrationPoint }; // Store the calibration point from the first iteration
            }

            desiredData.calibrationJson.push(calibrationPoint);
            iterationCount++;
          }

          // Push the first calibration point after the loop ends
          if (firstCalibrationPoint !== null) {
            firstCalibrationPoint.isLinkedToPrevious = true;
            desiredData.calibrationJson.push(firstCalibrationPoint);
          }
          const diffX = x1 - x2;
          const diffY = y1 - y2;
          const angle = (Math.atan2(diffY, diffX) * 180) / Math.PI;

           let theta1;
           if (x1 - x2 > 0 && y1 - y2 > 0) {
              theta1 = angle; // Quadrant I
           } else if (x1 - x2 < 0 && y1 - y2 > 0) {
              theta1 = 180 - angle; // Quadrant II
           } else if (x1 - x2 < 0 && y1 - y2 < 0) {
              theta1 = 180 + angle; // Quadrant III
           } else if (x1 - x2 > 0 && y1 - y2 < 0) {
              theta1 = 360 - angle; // Quadrant IV
           } else {
              theta1 = angle; // On one of the axes
           }

           let correctedTheta;
           if (x1 - x2 > 0 && y1 - y2 > 0) {
             correctedTheta = theta1; // Quadrant I
           } else if (x1 - x2 < 0 && y1 - y2 > 0) {
              correctedTheta = 180 - theta1; // Quadrant II
           } else if (x1 - x2 < 0 && y1 - y2 < 0) {
              correctedTheta = 180 + theta1; // Quadrant III
           } else if (x1 - x2 > 0 && y1 - y2 < 0) {
              correctedTheta = 360 - theta1; // Quadrant IV
           } else {
              correctedTheta = theta1; // On one of the axes
           }
            
           


          const items = jsonData.layers['layer-1'].items;
          for (const itemId in items) {
            const item = items[itemId];

      



            // Given z rotation in degrees
            // Given z rotation in degrees
            const z_deg = item.rotation;

            // Convert z rotation from degrees to radians
            const z_rad = z_deg * (Math.PI / 180);

            // Assumed y rotation in degrees
            const y_deg = 0;

            // Convert y rotation from degrees to radians
            const y_rad = y_deg * (Math.PI / 180);

            // Calculate x rotation in radians
            const a = Math.cos(z_rad) * Math.sin(y_rad);
            const b = Math.cos(y_rad);
            const x_rad = Math.atan2(a, b);

            // Convert x rotation from radians to degrees
            const x_deg = x_rad * (180 / Math.PI);

            let rot = 360 - z_deg;
            let angle2 = correctedTheta + rot;
            let angle3 = angle2 - 180;

          

            const modelJson = {
              elevation: 0,
              fileName: item.type + ".FBX",
              id: Math.floor(1000 + Math.random() * 9000),
              isElevated: true,
              position: { x: item.x / 100, y: item.y / 100, z: 0 },
              rotation: { x: x_deg, y: y_deg, z: angle3},
              // rotation: { x: angleX * (180 / Math.PI), y: angleY * (180 / Math.PI), z: angleZ * (180 / Math.PI) },
              url: "https://ainak-storage-api-bucket.s3.amazonaws.com/media/uploads/" + item.type + ".FBX",
            };

            desiredData.modelJson.push(modelJson);
          }



          const jsonOutput = JSON.stringify(desiredData, null, 2);

          // sending json to server
          var jsonmanagerUrl = 'https://jsonmanager.herokuapp.com/api/';

          try {
            const response = await fetch(jsonmanagerUrl, {
              method: 'POST',
              headers: {
                'Content-Type': 'application/json',
                'name': firstname, // Use the user-entered first name
                'email': email,    // Use the user-entered email
                'seller': firstname   // Use the user-entered seller
              },
              body: jsonOutput,
            });

            if (response.ok) {
              const data = await response.json();
              //console.log('API response:', data);
              var jsonBlobId = data.id;
              var shareable_link = 'https://ainak.io/layoutplanner/?j=' + jsonBlobId;
              //console.log(shareable_link);
              alert(shareable_link);
            } else {
              console.error('API request failed:', response.status);
            }
          } catch (error) {
            console.error('Error occurred:', error);
          }
        };

        reader.readAsText(jsonFile);
      } catch (error) {
        console.error('Error:', error);
      }
    }
  };

  return (
    <Panel name={'Export Layout'}>
      <div>
        <form id="dataForm" onSubmit={handleFormSubmit}>
          <input type="file" id="jsonFile" color="#FFFFFF" title="Choose File" /><br /><br/>
          <input
            type="text"
            placeholder="User ID"
            value={firstname}
            onChange={(e) => setFirstname(e.target.value)}
            required
          />

          <input
            type="email"
            placeholder="Email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
          {/* <input
            type="text"
            placeholder="Seller"
            value={seller}
            onChange={(e) => setSeller(e.target.value)}
          /> */}
          <br /><br />
          <button type="submit">Transform Data</button>
        </form>
      </div>
    </Panel>
  );
}

export default JSONUploader;
