import React, { useState } from 'react';
import Panel from './panel';

const getUrlVars = () => {
  const searchParams = new URLSearchParams(window.location.search);
  const vars = {};
  for (let [key, value] of searchParams) {
    vars[key] = value;
  }
  return vars;
};

const JsonConverter = () => {
  const shouldRenderCompanyColumn = getUrlVars()["c"] === "all";
  return (
    
      <div>
        {/* <form id="jsonForm"><br></br>
          <label htmlFor="linkInput">Enter Ainak Experience link:</label>
          <input
            type="text"
            id="linkInput"
            placeholder="Enter Link"
            value={linkInput}
            onChange={(e) => setLinkInput(e.target.value)}
          />
          <br /><br></br>
          <button type="button" onClick={fetchJsonData}>
            Fetch JSON Data
          </button>
        </form> */}
        <table  id='posts' className="unstyledTable">
          
            

              {/* <th>Name</th>
              <th>Email-id</th>
              <th>Link to Xperience</th> */}
              {shouldRenderCompanyColumn && <th>Company</th>}
              {/* <script type="text/javascript">
                if(getUrlVars()["c"] == "all")
                document.write("<th>Company</th>");
              </script> */}
            
          
        </table>
      </div>
    
  );
};

export default JsonConverter;
